import React from 'react';
import classes from "./AddTodo.module.css";

const addTodo = (props) => {
    let todoText;
    let handleClick = () => {
        if(todoText){
            console.log("function called handleClick: " + todoText);

            this.props.onAddButtonClick(todoText);
            todoText = "";
        }
    }

    return (<div className={classes.AddTodo}>
        <input type="text" className={classes.textBox} value={todoText}></input>
        <button className={classes.Button} onClick={() => handleClick()}>Add Todo</button>
    </div>)
};

export default addTodo;